import React from 'react';
import './HomePage.css';

class HomePage extends React.Component {
    state = {
        textFocus: '',
        puffIn: ''
    }

    getAnimations = () => {
        this.setState({textFocus: 'text-focus-in'});
       setTimeout(() => {
            console.log('waiting 1 second');
       }, 10000);
        this.setState({puffIn: 'puff-in-center'});
    }

    componentDidMount(){
        this.getAnimations();
    }

    render(){
        return(
            <div>
                <div className='container' id='presentationDiv'>
                    <hr className={this.state.textFocus} id='upperLine'></hr>
                    <h1 className={this.state.textFocus} id='title'>Hi, I'm Alin. I design and <br></br> build websites</h1>
                </div>
            </div>
        );
    }  
}

export default HomePage;